.btn {
  cursor: pointer;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
}

.btn--big-area::before {
  pointer-events: auto;
  position: absolute;
  content: var(--tw-content);
  inset: 0px;
}

.btn--link {
  display: inline-flex;
  font-weight: var(--font-weight-bold, 700);
  text-decoration-line: none;
  --tw-text-l: var(--color-accent-l);
  --tw-text-c: var(--color-accent-c);
  --tw-text-h: var(--color-accent-h);
  --tw-text-l-offset: 0;
  --tw-text-opacity: 1;
  color: oklch(clamp(0, calc(var(--tw-text-l) + var(--tw-text-l-offset)), 1) var(--tw-text-c) var(--tw-text-h) / var(--tw-text-opacity));
}

.btn--link:hover {
  text-decoration-line: underline;
}

.btn--link:focus {
  text-decoration-line: underline;
}

.btn:where(:not(.btn--link)) {
  border-radius: var(--rounded-button);
  padding-left: calc(1.31rem + 3.00 * var(--fluid-bp));
  padding-right: calc(1.31rem + 3.00 * var(--fluid-bp));
  padding-top: calc(0.33rem + 0.75 * var(--fluid-bp));
  padding-bottom: calc(0.33rem + 0.75 * var(--fluid-bp));
  text-align: center;
  font-family: var(--font-button);
  font-feature-settings: var(--font-button--feature);
  font-variation-settings: var(--font-button--variation);
  font-size: var(--f-sm);
  line-height: inherit;
  text-decoration-line: none;
  outline: 2px solid transparent;
  outline-offset: 2px;
  transition-property: color, background-color, border-color, text-decoration-color, fill, stroke, opacity, box-shadow, transform, filter, -webkit-backdrop-filter;
  transition-property: color, background-color, border-color, text-decoration-color, fill, stroke, opacity, box-shadow, transform, filter, backdrop-filter;
  transition-property: color, background-color, border-color, text-decoration-color, fill, stroke, opacity, box-shadow, transform, filter, backdrop-filter, -webkit-backdrop-filter;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 150ms;
}

.btn:where(:not(.btn--link)):disabled {
  cursor: not-allowed;
  opacity: 0.5;
}

.btn--inline:where(:not(.btn--link)) {
  display: inline-flex;
}

.btn--block:where(:not(.btn--link)) {
  display: flex;
  width: 100%;
}

.btn--theme {
  --tw-bg-l: var(--color-back-l);
  --tw-bg-c: var(--color-back-c);
  --tw-bg-h: var(--color-back-h);
  --tw-bg-l-offset: 0;
  --tw-bg-opacity: 1;
  background-color: oklch(clamp(0, calc(var(--tw-bg-l) + var(--tw-bg-l-offset)), 1) var(--tw-bg-c) var(--tw-bg-h) / var(--tw-bg-opacity));
  --tw-text-l: var(--color-front-l);
  --tw-text-c: var(--color-front-c);
  --tw-text-h: var(--color-front-h);
  --tw-text-l-offset: 0;
  --tw-text-opacity: 1;
  color: oklch(clamp(0, calc(var(--tw-text-l) + var(--tw-text-l-offset)), 1) var(--tw-text-c) var(--tw-text-h) / var(--tw-text-opacity));
}

.btn--theme:hover {
  --tw-bg-l-offset: .1;
}

.btn--theme:focus {
  --tw-bg-l-offset: .1;
}

.btn--ghost {
  --btn-text: oklch(var(--color-front-l) var(--color-front-c) var(--color-front-h));
  --btn-bg: oklch(var(--color-back-l) var(--color-back-c) var(--color-back-h));

  background: transparent;
  color: var(--btn-text);
  text-shadow: 0 0 15px var(--btn-bg);
  box-shadow: inset 0 0 0 2px var(--btn-text);
}

.btn--ghost:hover,
  .btn--ghost:focus {
    background: var(--btn-text);
    color: var(--btn-bg);
    text-shadow: none;
  }
